<template>
  <div class="list-template-container">
    <div class="content">
      <div class="theme_show_wrap appear_setting">
        <!-- <div class="navbar edit_navbar" style="height: 80px;"></div> -->
        <div class="preview_iframe">
            <!-- <div class="phone">
              <iframe frameborder="0"
                ref="iframe"
                name="iframe"
                :src="'//localhost:8863/#/site/' + $route.params.sid"
              ></iframe>
            </div> -->
            <div class="site" ref="site">
              <iframe frameborder="0"
                ref="iframe"
                name="iframe"
                :src="baseUrl + $route.params.sid"
              ></iframe>
            </div>
        </div>
      </div>
    </div>
    <set-appear v-on:refresh-iframe="refreshIframe()"></set-appear>
  </div>
</template>

<script>
  import api from '~/api/api';
  import setAppear from '~/components/setAppear.vue'

  export default {
    data() {
      return {
        baseUrl: process.env.NODE_ENV == 'production' ? '//siteres.ztgame.com/survey/fe/#/site/' : '//survey.dev.ztgame.com/fe/#/site/'
      };
    },
    components: {
      setAppear
    },
    mounted() {
      this.$refs.site.style.height = (document.body.clientHeight - 106) + 'px';
    },
    methods: {
      refreshIframe() {
        // :src="'//survey.dev.ztgame.com/fe/#/' + $route.params.sid"
        this.$refs.iframe.contentWindow.postMessage("refresh");
        // this.$refs.iframe.scr = `//localhost:8863/#/2c74c28226f303e4#${new Date()}`;
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  .list-template-container {
    min-width 1306px
    margin 0 auto
    .content {
      .appear_setting {
          box-sizing: border-box;
      }
      .theme_show_wrap {
          padding: 16px 260px 0 72px;
          .preview_iframe {
            text-align: center;
            .phone {
              position: relative;
              margin-top: 10px;
              display: inline-block;
              width: 744px;
              height: 372px;
              background-image: url(./appearset_bgc_big.png);
              background-size: 744px 372px ;
              iframe {
                width: 568px;
                height: 318px;
                margin: 27px 0 0 -28px;
              }
            }

            .site {
              position: relative;
              margin-top: 10px;
              display: inline-block;
              min-width: 974px;
              min-height: 400px;
              iframe {
                width: 100%;
                height: 100%;
              }
            }
          }
      }
    }
  }
</style>



