<template>
    <div class="set_appear_main_wrap slideInRight animated" style="opacity: 1; display: block; right: 0px;">
        <div class="question_setting">
            <div class="q_title">
                外观设置
            </div>
            <div class="block">
                <el-collapse v-model="activeName" accordion>
                    <el-collapse-item title="头图" name="1">
                        <div class="block_sb mb-25">
                            <div>头图设置</div>
                            <label class="upload" for="uplodeheader" v-if="!header">
                                上传图片<input type="file" name="uplodeheader" @change="uploadFile($event, 'header')" accept="image/png, image/jpeg, image/jpg, image/gif">
                            </label>
                            <label class="lable_style" for="uplodebackground" v-if="header">
                                <img :src="header">
                                <i class="icon_wj delete_icon" @click="deleteImg('header')"></i>
                            </label>
                        </div>
                    </el-collapse-item>
                    <el-collapse-item title="背景" name="2">
                        <div class="block_sb">
                            <div>背景设置</div>
                            <div class="tab_layout">
                                <a class="btn_text_icon btn_layout btn-layout-single"
                                    v-for="item in types"
                                    :key="item.value"
                                    :class="{'checked': item.value == selectedType}"
                                    @click="selectedType = item.value"
                                >{{item.label}}</a>
                            </div>
                        </div>
                        <div class="block_sb fadeIn animated" v-show="selectedType == 1">
                            <div>背景图片</div>
                            <label class="upload" for="uplodeheader" v-if="!bgSrc">
                                上传图片<input type="file" name="uplodeheader" @change="uploadFile($event, 'bgSrc')" accept="image/png, image/jpeg, image/jpg, image/gif">
                            </label>
                            <label class="lable_style" for="uplodebackground" v-if="bgSrc">
                                <img :src="bgSrc">
                                <i class="icon_wj delete_icon" @click="deleteImg('bgSrc')"></i>
                            </label>
                        </div>
                        <div class="block_sb fadeIn animated" v-show="selectedType == 2">
                            <div>背景颜色</div>
                            <el-color-picker v-model="color" size="mini"></el-color-picker>
                        </div>
                        <div class="block_sb">
                            <div>底色透明度</div>
                        </div>
                        <el-slider v-model="opacity" show-input :show-input-controls="false" @change="refresh"></el-slider>
                    </el-collapse-item>
                    <el-collapse-item title="文字" name="3">
                        <div class="block_sb mb-25 fadeIn animated">
                            <div>文字颜色</div>
                            <el-color-picker v-model="textColor" size="mini"></el-color-picker>
                        </div>
                    </el-collapse-item>
                    <el-collapse-item title="选项" name="4">
                        <div class="block_sb fadeIn animated">
                            <div>选项背景色</div>
                            <el-color-picker v-model="optionColor" size="mini"></el-color-picker>
                        </div>
                        <div class="block_sb mb-25 fadeIn animated">
                            <div>选项选中色</div>
                            <el-color-picker v-model="optionHighlightColor" size="mini"></el-color-picker>
                        </div>
                    </el-collapse-item>
                </el-collapse>
            </div>
        </div>
    </div>
</template>

<script>
  import api from '~/api/api';
  import {mapState} from 'vuex';
  import * as types from '~/store/mutation-type';
  import store from '~/store/index';

  export default {
    computed: {
        ...mapState({
            selectedItem: state => state.selectedItem,
            questions: state => state.questions
        })
    },
    data() {
        return {
            activeName: '1',
            selectedType: 1,
            opacity: 100,
            color: '',
            optionColor: '',
            optionHighlightColor: '',
            textColor: '',
            bgSrc: '',
            header: '',
            types: [
                {value: 1, label: '上传图片'},
                {value: 2, label: '选择颜色'},
            ]
        }
    },
    mounted() {
        this.init();
    },
    watch: {
        color(val) {
            this.refresh();
        },
        optionColor() {
            this.refresh();
        },
        optionHighlightColor() {
            this.refresh();
        },
        textColor() {
            this.refresh();
        },
    },
    methods: {
        uploadFile(e, target) {
            api.upload(e.target.files).then(res => {
                if (res) {
                    this[target] = res.data[0].pic;
                    this.refresh();
                }
            });
        },
        refresh() {
            this.saveStyle().then(res => {
                this.$emit('refresh-iframe');
            });
            
        },
        init() {
            if(!this.$route.params.sid) return;
            api.getInfo(this.$route.params.sid).then(res => {
                if(!res) return;
                let style = res.data.style;
                if(!style) return;
                if (style.color) {
                    this.selectedType = 2;
                } else {
                    this.selectedType = 1;
                }
                this.opacity = style.opacity;
                this.color = style.color;
                this.bgSrc = style.bgSrc;
                this.header = style.header;
                this.optionColor = style.optionColor;
                this.optionHighlightColor = style.optionHighlightColor;
                this.textColor = style.textColor
            });
        },
        deleteImg(target) {
            this[target] = '';
            this.refresh();
        },
        saveStyle() {
            let style = {
                opacity: this.opacity,
                header: this.header
            };
            if(this.selectedType == 1) {
                style.bgSrc = this.bgSrc;
                style.color = '';
            } else {
                style.color = this.color ? this.color : '';
                style.bgSrc = '';
            }
            style.optionHighlightColor = this.optionHighlightColor ? this.optionHighlightColor : '';
            style.optionColor = this.optionColor ? this.optionColor : '';
            style.textColor = this.textColor ? this.textColor : '';
            return new Promise((resolve, reject) => {
                api.updateSurvery({sid: this.$route.params.sid, style}).then(res => {
                    resolve();
                })
            })
        }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .set_appear_main_wrap {
    display: none;
    width: 260px;
    overflow: auto;
    background-color: #ffffff;
    border-left: solid 1px #dde5ed;
    position: fixed;
    right: -260px;
    top: 66px;
    bottom: 0;
    padding: 30px 0;
    line-height: 1.38;
        .q_title {
            display: inline-block;
            color: #ffffff;
            font-size: 14px;
            padding: 5px 12px;
            border-radius: 2px;
            background-color: #495165;
            margin-left: 18px;
        }
        .block {
            margin-top: 30px;
            .el-collapse-item__header {
                padding: 0 20px;
            }
            .mb-25 {
                margin-bottom -25px
            }
            .el-slider {
                width 259px;
                height 48px
                margin-bottom -25px
                margin-top: -16px;
                padding: 0 20px;
                background-color: #fafafa;
                .show-input {
                    margin-right: 70px;
                }
                &__input {
                    color: #aaa;
                    width: 50px;
                    height: 24px;
                    display: inline-block;
                    text-align: center;
                    .el-input__inner {
                        padding 0 5px
                    }
                }
            }
            &_sb {
                display flex
                justify-content space-between
                align-items center
                border-top: 1px solid #ebeef5;
                line-height 48px
                background-color: #fafafa;
                color grey
                font-size 12px
                padding 0 20px
                height 48px
                .tab_layout {
                    border-radius: 2px;
                    overflow: hidden;
                    vertical-align: middle;
                    font-size: 0;
                    .btn_layout {
                        display: inline-block;
                        border-left: solid 1px #aaaaaa;
                        border-top: solid 1px #aaaaaa;
                        border-bottom: solid 1px #aaaaaa;
                        background-color: #ffffff;
                        text-align: center;
                        font-size: 12px;
                        line-height: 22px;
                        vertical-align: middle;
                        cursor: pointer;
                        padding 3px 5px
                        &.checked {
                            color: #ffffff;
                            background-color: #2672ff;
                            border-color: #2672ff;
                            transition all 0.3s ease;
                        }
                        &:first-child {
                            border-top-left-radius: 4px;
                            border-bottom-left-radius: 4px;
                        }
                        &:last-child {
                            border-right: solid 1px #aaaaaa;
                            border-top-right-radius: 4px;
                            border-bottom-right-radius: 4px;
                        }
                    }
                }
                .lable_style {
                    color: #2672FF;
                    font-size: 12px;
                    cursor: pointer;
                    position: relative;
                    height: 30px;
                    img {
                        height: 30px;
                    }
                    .icon_wj {
                        width: 24px;
                        height: 24px;
                        background: url(../images/icons.png) no-repeat;
                        background-size: 520px auto;
                        &.delete_icon {
                            width: 17px;
                            height: 18px;
                            position: absolute;
                            background-position: -388px -169px;
                            right: -8.5px;
                            top: -9px;
                            cursor: pointer;
                        }
                    }
                }
                .upload {
                    position relative
                    color: #2672FF;
                    font-size: 12px;
                    cursor: pointer;
                    overflow: hidden;
                    height: 48px;
                    line-height: 48px;
                    cursor pointer
                    input[type="file"] {
                        position: absolute;
                        width: 100%;
                        height: 100%;
                        top: 0;
                        left: 0;
                        opacity: 0;
                        cursor: pointer;
                    }
                }
            }
        }
    }
</style>



